.button {
  font-family: 'Open Sans', sans-serif;
  font-size: 13px;
  background-color: #236aea;
  color: white;
  border: 1px solid #ddddde;
  border-radius: 4px;
  padding: 10px 15px;
  min-width: 170px;
  cursor: pointer;
  margin: 16px 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-transition: 0.2s background;
  -o-transition: 0.2s background;
  transition: 0.2s background; }
  .button svg {
    fill: #ffffff;
    margin-right: 0.5em;
    -webkit-transition: 0.2s fill;
    -o-transition: 0.2s fill;
    transition: 0.2s fill; }
  .button .icon {
    margin-right: 0.75em; }
  .button:hover {
    background: rgba(35, 106, 234, 0.85); }
  .button.inactive {
    background: transparent;
    border: 1px solid #2a6ee7; }
    .button.inactive svg {
      fill: #2a6ee7; }
    .button.inactive:hover {
      background-color: #236aea; }
      .button.inactive:hover svg {
        fill: white; }



