.api {
  width: 100%; }
  .api-action {
    cursor: pointer;
    display: inline-block; }
    .api-action:not(:last-child) {
      margin-right: 20px; }
    .api-action-red {
      color: #df4545; }
    .api-action-blue {
      color: #236aea; }
  .api thead tr td {
    color: #364041;
    font-weight: 700;
    text-transform: uppercase;
    font-size: 11px;
    padding-bottom: 20px;
    border-bottom: 1px solid #ddddde; }
  .api tbody tr td {
    padding: 12px 0; }
    .api tbody tr td:nth-child(1) {
      color: #364041;
      font-size: 13px;
      font-weight: 700; }
    .api tbody tr td:nth-child(2) {
      color: #878787;
      font-size: 11px; }
    .api tbody tr td:nth-child(3) {
      font-size: 13px; }
  .api tr td:last-child {
    width: 1%;
    white-space: nowrap; }



